.osc-slider-container {
  /* padding: 30px; */
  position: relative;
  height: 44px;
  cursor: pointer;
  /* For debugging */
  /* border: solid 1px green; */
  /* Slider track styling in background */
  /* Styling of handle, based on https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/ */
}
.osc-slider-container .osc-slider-input-range {
  height: 44px;
  position: absolute;
  margin: 0;
}
.osc-slider-container .osc-slider-track-container {
  position: absolute;
  height: 42px;
  left: 0;
  right: 0;
  /* pointer-events: none; */
  /* For debugging */
  /* border: solid 1px purple; */
}
.osc-slider-container .osc-slider-track-container > div {
  position: absolute;
}
.osc-slider-container .osc-slider-track-dot-start {
  width: 22px;
  height: 22px;
  background-color: #004699;
  border-radius: 11px;
}
.osc-slider-container .osc-slider-track-dot-end {
  width: 22px;
  height: 22px;
  background-color: white;
  border: solid 2px #004699;
  border-radius: 11px;
  box-sizing: border-box;
}
.osc-slider-container .osc-slider-track-dot-center {
  margin: 11px auto 11px auto;
  left: 0;
  right: 0;
}
.osc-slider-container .osc-slider-track-dot-left {
  margin: 11px 0 11px 11px;
  left: 0;
  /* top: 0; */
  /* bottom: 0; */
}
.osc-slider-container .osc-slider-track-dot-right {
  margin: 11px 11px 11px 0;
  right: 0;
}
.osc-slider-container .osc-slider-track {
  height: 2px;
  left: 22px;
  right: 22px;
  /* width: 100%; */
  background-color: #004699;
  margin: 21px 0 21px 0;
}
.osc-slider-container .osc-slider-track-progress-container {
  left: 22px;
  right: 22px;
  bottom: 0;
  top: 0;
}
.osc-slider-container .osc-slider-track-progress {
  height: 10px;
  width: 50%;
  background-color: #004699;
  margin: 17px 0 17px 0;
  left: 50%;
  position: absolute;
  transform-origin: left;
}
.osc-slider-container input[type=range] {
  -webkit-appearance: none;
  /* Hides the slider so that custom slider can be made */
  width: 100%;
  /* Specific width is required for Firefox. */
  background: transparent;
  /* Otherwise white in Chrome */
}
.osc-slider-container input[type=range]:focus {
  outline: none;
  /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}
.osc-slider-container input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  /* Hides the slider so custom styles can be added */
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.osc-slider-container input[type=range]::-webkit-slider-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  -webkit-appearance: none;
  height: 44px;
  width: 44px;
  background-color: #004699;
  cursor: pointer;
  border-radius: 22px;
}
.osc-slider-container input.osc-slider-untouched[type=range]::-webkit-slider-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  border: solid 2px #004699;
}
.osc-slider-container input[type=range]::-moz-range-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  box-sizing: border-box;
  height: 44px;
  width: 44px;
  background-color: #004699;
  cursor: pointer;
  border-width: 0;
  border-radius: 22px;
}
.osc-slider-container input.osc-slider-untouched[type=range]::-moz-range-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  border: solid 2px #004699;
  border-radius: 22px;
}
.osc-slider-container input[type=range]::-ms-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  height: 44px;
  width: 44px;
  background-color: #004699;
  cursor: pointer;
  border-radius: 22px;
}
.osc-slider-container input[type=range]::-ms-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  border: solid 2px #004699;
}
.osc-slider-container input[type=range]::-webkit-slider-runnable-track {
  cursor: pointer;
}
.osc-slider-container input[type=range]::-moz-range-track {
  cursor: pointer;
}
.osc-slider-container input[type=range]::-ms-track {
  cursor: pointer;
}
.osc-slider-container input[type=range]::-moz-focus-outer {
  border: 0;
}
.osc-error .osc-slider-container input.osc-slider-untouched[type=range]::-webkit-slider-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  border: solid 2px #ec0000;
}
.osc-error .osc-slider-container input.osc-slider-untouched[type=range]::-moz-range-thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  border: solid 2px #ec0000;
}
.osc-error .osc-slider-track-dot-start {
  background-color: #ec0000;
}
.osc-error .osc-slider-track-dot-end {
  border: solid 2px #ec0000;
}
.osc-error .osc-slider-track {
  background-color: #ec0000;
}
